.carousel-container{
    width: 100%;
    display: flex;
    flex-direction: column;
    position: relative;
    background-color: green;
}


.image{
    width: 100%;
    height: 65vh;
    object-fit: cover;
    overflow:hidden;
}

.number {
    color: #000000;
    font-size: calc(10px + 2vmin);
    padding: 5px 5px;
    position: relative;
}

.text {
    color: #000000;
    font-size: calc(10px + 2vmin);
    padding: 5px 5px;
    position: relative;
}

.next {
    right: 0;
}
.prev {
    left: 0;
}

.prev, .next {
    cursor: pointer;
    position: absolute;
    top: 50%;
    padding: 16px;
    color: white;
    font-weight: bold;
    font-size: 18px;
    transition: 0.6s ease;
    border-radius: 0 3px 3px 0;
    user-select: none;
    background-color: black;
    opacity: 75%;
}
.prev:hover, .next:hover {
    background-color: grey;
}





.item-enter {
    opacity: 0;
}
.item-enter-active {
    opacity: 1;
    transition: opacity 500ms ease-in;
}
.item-exit {
    opacity: 1;
}
.item-exit-active {
    opacity: 0;
    transition: opacity 500ms ease-in;
}